import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { RelativeUserSelectComponent } from '../relative-user-select/relative-user-select.component';

@Component({
  selector: 'app-relative-users',
  templateUrl: './relative-users.component.html',
  styleUrls: ['./relative-users.component.scss'],
})
export class RelativeUsersComponent implements OnInit {
  @Input() users: any;
  @Input() mobile: any;

  selectedUser: any;

  constructor(private modalCtrl: ModalController) {}

  ngOnInit() {}

  onSelect(item) {
    this.selectedUser = item;
    setTimeout(() => {
      this.modalCtrl.dismiss(this.selectedUser);
    }, 500);
  }

  // Opening relative user sheet
  async openUserModal() {
    const modal = await this.modalCtrl.create({
      component: RelativeUserSelectComponent,
      cssClass: 'user-modal',
      componentProps: {
        users: this.users,
      },
      backdropDismiss: false,
    });
    await modal.present();

    modal.onDidDismiss().then((res) => {
      if (res?.data) {
        this.modalCtrl.dismiss(res?.data);
      }
    });
  }
}
