import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';

@Component({
  selector: 'app-relative-user-select',
  templateUrl: './relative-user-select.component.html',
  styleUrls: ['./relative-user-select.component.scss'],
})
export class RelativeUserSelectComponent implements OnInit {
  @Input() users: string;
  selectedUser: any;
  term: any;

  constructor(private modalCtrl: ModalController) {}

  ngOnInit() {}

  onSelect(item) {
    this.selectedUser = item;
  }

  close() {
    this.modalCtrl.dismiss(this.selectedUser);
  }

  back(){
    this.modalCtrl.dismiss();
  }
}
